import AppMod from "@reneos/app"
const App = AppMod.default || AppMod
//import { computed } from "vue"


App.menu.main.push({
  id: "pack",
  label: "Pack",
  type: "group",
  icon: "view-tile",
  /*hidden: computed(() => {
    return !App.instances.instances.length
  }),*/
  ext: [
    {
      component: "SimpleButton",
      props: {
        tooltip: "Break model",
        icon: {
          value: "view-tile",
          size: "50%"
        },
        handlers: {
          click: () => {
            App.engine.post({
              type: "packer", method: "Breaks"
            })
          }
        }
      }
    }
  ],
  handlers: {
    "enable": (m) => {
      App.engine.post({
        type: "packer", method: "Activate"
      })
    },
    "disable": () => {
      App.engine.post({
        type: "packer", method: "Deactivate"
      })
    }
  }
})

if (process.env.NODE_ENV === 'development') {
  App.engine.post({
    type: "imports",
    scripts: [`${window.location.href}packer/main.js`]
  })
}
else {
  /*App.engine.post({
    type: "imports",
    scripts: [`${window.location.href}pack/bimpack.js`]
  })*/
}